import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';
import * as Instafeed from 'instafeed.js';

import { RandomFactService } from '../../services/random-fact.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  currentExp: any;
  public randomFactsList = [];

  constructor(private _randomFactService: RandomFactService) {
  }

  ngOnInit() {

    this._randomFactService.getData()
      .subscribe(data=> {
        this.randomFactsList = data;
        console.log(this.randomFactsList);
      })


    // Calculate work experiance
    var joinDate = moment([2016, 1, 15]);
    var currentDate = moment([new Date().getFullYear(), new Date().getMonth(), new Date().getDate()]);
    var years = currentDate.diff(joinDate, 'year');
    joinDate.add(years, 'years');
    var months = currentDate.diff(joinDate, 'months');
    joinDate.add(months, 'months');
    var days = currentDate.diff(joinDate, 'days');
    this.currentExp = years + ' years ' + months + ' months ' + days + ' days';

    // Instagram feed
    var feed = new Instafeed({
      get: 'user',
      userId: '1180789383',
      clientId: '8421ffd76f4b4260af2fd2d0ad7f2f3b',
      accessToken: '1180789383.1677ed0.63820bf341984c9ba329aa98d2947d93 ',
      template: `
        <div class="col instafeed-w col-md-3 col-sm-6 col-12" style="height:200px">
            <a href="{{link}}" target="_blank">
              <img style="width:100%;height:100%;padding:10px 0;object-fit:cover" src="{{image}}" />
            </a>          
        </div>`,
      limit: 8,
      resolution: 'standard_resolution'
    });
    feed.run();

    
  }

}
