import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

export interface workHistory {
  company: "",
  profile: "",
  year: ""
}

export interface educationHistory {
  course: "",
  year: ""
}

export interface skills {
  lang: "",
  percentage: ""
}

@Injectable({
  providedIn: 'root'
})

export class ResumeService {
  
  constructor(private http: HttpClient) { }

  getEducationHistory(): Observable<educationHistory[]>{
    return this.http.get<educationHistory[]>('https://raw.githubusercontent.com/karthik-padav/karthik-padav.github.io/master/assets/data/educationHistory.json');
  }

  getWorkHistory(): Observable<workHistory[]>{
    return this.http.get<workHistory[]>('https://raw.githubusercontent.com/karthik-padav/karthik-padav.github.io/master/assets/data/workHistory.json');
  }

  getSkillList(): Observable<skills[]>{
    return this.http.get<skills[]>('https://raw.githubusercontent.com/karthik-padav/karthik-padav.github.io/master/assets/data/skills.json');
  }
}
