module.exports = ".resume-wrapper{\r\n    padding: 30px;\r\n}\r\n.header {\r\n    margin-bottom: 15px;\r\n    border-radius: 6px;\r\n    -webkit-border-radius: 6px;\r\n    -moz-border-radius: 6px;\r\n    overflow: hidden;\r\n}\r\n.header .nav-tabs {\r\n    border: none;\r\n}\r\n.navbar {\r\n    background-color: #ffffff;\r\n    border: 0;\r\n    border-radius: 4px;\r\n    overflow: hidden;\r\n}\r\n.navbar-toggler {\r\n    color: #ffb100;\r\n    margin-top: 22px;\r\n    margin-left: 18px;\r\n}\r\n.header ul li a {\r\n    font-size: 14px;\r\n    font-weight: bold;\r\n    color: #000;\r\n    padding: 0 10px;\r\n    line-height: 73px;\r\n    display: block;\r\n}\r\n.header ul li a:hover {\r\n    color: #ffc500 !important;\r\n    text-decoration: none;\r\n}\r\n.header .nav-tabs a.home-btn {\r\n    padding: 0px 24px;\r\n    background-color: #ffc500;\r\n    margin-right: 10px;\r\n    transition: all ease 0.4s;\r\n    float: left;\r\n}\r\n.header a.home-btn i {\r\n    font-size: 21px;\r\n}\r\n.tab-content {\r\n    padding: 0;\r\n    background-color: #ffffff;\r\n    border-radius: 6px;\r\n    -moz-border-radius: 6px;\r\n    -webkit-border-radius: 6px;\r\n    -o-border-radius: 6px;\r\n    box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);\r\n    -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);\r\n    -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);\r\n}\r\n.header ul li a.home-btn:hover {\r\n    color: #ffffff !important;\r\n}\r\nfooter .footer {\r\n    background: #1e1e1e;\r\n    color: #fff;\r\n    border-radius: 8px;\r\n    -webkit-border-radius: 8px;\r\n    -moz-border-radius: 8px;\r\n}\r\n#social-icons {\r\n    margin-right: 0;\r\n}\r\n#social-icons a {\r\n    font-size: 110%;\r\n    padding: 0 10px;\r\n    color: #787878;\r\n    line-height: 73px;\r\n}\r\n.nav>li>a:focus, .nav>li>a,\r\n.nav>li>a:focus, .nav>li>a:hover {\r\n    background-color: transparent;\r\n    border: 0;\r\n}\r\n/* .header ul li.active a {\r\n    color: #ffc500!important;\r\n} */\r\n@media only screen and (max-width: 768px){\r\n    .navbar {\r\n        background-color: #ffc500;\r\n    }\r\n    .navbar-toggle .icon-bar {\r\n        background: #ffffff;\r\n    }\r\n    .nav-tabs>li {\r\n        float: left;\r\n        width: 100%;\r\n    }\r\n    .header .nav-tabs a.home-btn {\r\n        width: 100%;\r\n        margin: 0;\r\n        padding: 0 10px;\r\n    }\r\n    .header ul li a {\r\n        line-height: 3;\r\n    }\r\n    .header ul li a:hover {\r\n        color: #ffffff !important;\r\n    }\r\n    .nav.navbar-nav#social-icons {\r\n        display: none;\r\n    }\r\n}\r\n@media only screen and (max-width: 425px){\r\n    .resume-wrapper {\r\n        padding: 10px;\r\n    }\r\n    .footer,\r\n    .footer #social-icons {\r\n        text-align: center !important;\r\n    }\r\n}"