import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

export interface randomFacts {
  fact: string;
}

@Injectable({
  providedIn: 'root'
})
export class RandomFactService {

  constructor(private http: HttpClient) { }

  getData(): Observable<randomFacts[]>{
    return this.http.get<randomFacts[]>('https://raw.githubusercontent.com/karthik-padav/karthik-padav.github.io/master/assets/data/randomFacts.json');
  }
}
