import { Component, OnInit } from '@angular/core';
import { ResumeService } from '../../services/resume.service';

@Component({
  selector: 'app-resume',
  templateUrl: './resume.component.html',
  styleUrls: ['./resume.component.css']
})
export class ResumeComponent implements OnInit {

  public educationHistoryList = [];
  public workHistoryList = [];
  public skillList = [];

  constructor(
    private _educationHistoryService:ResumeService,
    private _skillService:ResumeService,
    private _workHistoryService:ResumeService) { }

  ngOnInit() {
    this._educationHistoryService.getEducationHistory()
      .subscribe(data => {
        this.educationHistoryList = data;
        console.log(this.educationHistoryList);
      })

      this._workHistoryService.getWorkHistory()
      .subscribe(data => {
        this.workHistoryList = data;
        console.log(this.workHistoryList);
      })

      this._skillService.getSkillList()
      .subscribe(data => {
        this.skillList = data;
        console.log(this.skillList);
      })
  }

}
